}
#editor .interface-interface-skeleton__header .edit-post-header__toolbar .edit-post-header-toolbar {
  flex-grow: initial;
}
#editor .interface-interface-skeleton__header .edit-post-header__toolbar .edit-post-visual-editor__post-title-wrapper {
  margin: 0;
  margin-left: 5rem;
}
#editor .interface-interface-skeleton__header .edit-post-header__toolbar .edit-post-visual-editor__post-title-wrapper h1 {
  margin: 0 auto;
}
#editor .interface-interface-skeleton__header .edit-post-header__toolbar .edit-post-visual-editor__post-title-wrapper h1:after {
  content: none;
}
#editor .interface-interface-skeleton__header .edit-post-header__toolbar #devicesButtonGroupContainer {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 5rem;
}
#editor .interface-interface-skeleton__header .edit-post-header__toolbar #devicesButtonGroupContainer .devicesInfo {
  margin-right: 30px;
  display: flex;
  align-items: center;
}
#editor .interface-interface-skeleton__header .edit-post-header__toolbar #devicesButtonGroupContainer .devicesInfo .dashicon {
  font-size: 1.2em;
  height: auto;
  width: auto;
  margin: 2px 5px 0 5px;
}
#editor .interface-interface-skeleton__header .edit-post-header__toolbar #devicesButtonGroupContainer .devicesButtonGroup button {
  box-shadow: inset 0 0 0 1px var(--border300, rgb(235, 235, 235));
  background: var(--background300, rgb(245, 245, 245));
}
#editor .interface-interface-skeleton__header .edit-post-header__toolbar #devicesButtonGroupContainer .devicesButtonGroup button.is-pressed {
  background: var(--wp-admin-theme-color);
}

#editor .flex {
  display: flex;
}
#editor.resizable .is-mode-visual .interface-interface-skeleton__body {
  width: 100%;
  transition: width 0.4s;
}
#editor.resizable .is-mode-visual .interface-interface-skeleton__body:has(#abt-component-edit-zone .edit-zone__inner) {
  width: calc(100% - 700px);
}
#editor .interface-interface-skeleton__body .edit-post-visual-editor__post-title-wrapper {
  display: none;
}
#editor .editor-styles-wrapper.block-editor-writing-flow {
  padding: 0 !important;
  background: var(--abt-background-editor, "white");
}
#editor .editor-styles-wrapper.block-editor-writing-flow .is-root-container.is-layout-flow {
  padding: 2rem 0;
  border-left: 1px dashed rgba(128, 128, 128, 0.5);
  border-right: 1px dashed rgba(128, 128, 128, 0.5);
}
#editor .editor-styles-wrapper.block-editor-writing-flow .is-root-container.is-layout-flow .block-editor-block-list__block {
  width: calc(100% - 4rem) !important;
  max-width: calc(100% - 4rem) !important;
}
#editor .editor-styles-wrapper.block-editor-writing-flow .is-root-container.is-layout-flow > .block-list-appender {
  width: calc(100% - 4rem) !important;
  max-width: calc(100% - 4rem) !important;
}
#editor .editor-styles-wrapper.block-editor-writing-flow .is-root-container.is-layout-flow > .block-list-appender .block-editor-default-block-appender__content {
  display: none;
}
#editor .editor-styles-wrapper.block-editor-writing-flow .is-root-container.is-layout-flow > .block-list-appender .block-editor-inserter {
  position: relative;
  display: block;
}
#editor .editor-styles-wrapper.block-editor-writing-flow .is-root-container.is-layout-flow > .block-list-appender .block-editor-inserter .components-button {
  width: 100%;
  height: auto;
  padding: 12px;
  background: none;
}

.wpe-dropdown-container {
  margin-right: 10px;
  flex-grow: 1;
}
.wpe-dropdown-container:last-child {
  margin-right: 0;
}
.wpe-dropdown-container .wpe-dropdown-wrapper {
  position: relative;
  border: 1px solid var(--border300, rgb(235, 235, 235));
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-header {
  position: relative;
  background: white;
  height: 34px;
  cursor: pointer;
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-header .wpe-dropdown-header-title {
  padding: 8px 14px 0 14px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-header .dashicon {
  position: absolute;
  top: 8px;
  right: 5px;
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-list {
  position: absolute;
  z-index: 2;
  background: white;
  border: 1px solid var(--border300, rgb(235, 235, 235));
  top: 34px;
  left: -1px;
  right: -1px;
  padding: 10px;
  box-shadow: 0px 10px 25px rgba(0, 0, 0, 0.15);
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-list .wpe-dropdown-list-item-parent {
  margin-bottom: 10px;
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-list .wpe-dropdown-list-item-parent:last-child {
  margin-bottom: 0;
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-list .wpe-dropdown-list-item-parent .wpe-dropdown-list-item-cat {
  color: rgb(160, 160, 160);
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-list .wpe-dropdown-list-item-parent .wpe-dropdown-list-item-children-list {
  padding: 0px 10px;
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-list .wpe-dropdown-list-item {
  padding: 4px;
  border-bottom: 1px solid var(--border300, rgb(235, 235, 235));
  cursor: pointer;
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-list .wpe-dropdown-list-item:last-child {
  border-bottom: none;
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-list .wpe-dropdown-list-item:hover {
  background: rgb(240, 240, 240);
}
.wpe-dropdown-container .wpe-dropdown-wrapper .wpe-dropdown-list .wpe-dropdown-list-item .wpe-dropdown-list-item-inner {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.components-modal__screen-overlay {
  z-index: 1000001;
}

.wpe-modal {
  min-width: 600px;
  min-height: 240px;
  padding: 0;
}
.wpe-modal .components-modal__content {
  padding: 0;
}
.wpe-modal .components-modal__content .components-modal__header {
  padding: 0 50px;
  border-bottom: 1px solid var(--border300, rgb(235, 235, 235));
}
.wpe-modal .components-modal__content .components-modal__body {
  padding: 50px 50px 120px;
}
.wpe-modal .components-modal__content .components-modal__body .components-toggle-control {
  margin-top: 20px;
}
.wpe-modal .components-modal__content .components-modal__footer {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 10;
  height: 60px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid var(--border300, rgb(235, 235, 235));
  background: var(--background300, rgb(245, 245, 245));
  padding: 0 50px;
}
.wpe-modal .components-modal__content .components-modal__footer .alignRight {
  margin-left: auto;
}

.DraftEditor-Container {
  background-color: rgb(248, 248, 248);
  border: 1px solid var(--border300, rgb(235, 235, 235));
  font-family: "Georgia", serif;
  font-size: 14px;
  padding: 15px;
}
.DraftEditor-Container .DraftEditor-controls-row {
  margin-bottom: 10px;
}
.DraftEditor-Container .DraftEditor-controls-row .label {
  font-size: 13px;
}
.DraftEditor-Container .DraftEditor-controls-row .label.label-background {
  display: inline-block;
  background: var(--border300, rgb(235, 235, 235));
  padding: 1px 7px 0px 7px;
  border-radius: 6px 6px 0px 0px;
  border: 1px solid var(--border300, rgb(235, 235, 235));
}
.DraftEditor-Container .DraftEditor-controls-row .DraftEditor-controls-row-inner {
  display: flex;
  justify-content: space-between;
}
.DraftEditor-Container .DraftEditor-controls-row .DraftEditor-controls-row-inner .DraftEditor-styleButton {
  cursor: pointer;
  margin: 0 1px 0 0 !important;
  padding: 2px 8px !important;
  display: inline-block;
  border: 1px solid rgba(0, 124, 186, 0);
  border-radius: 6px;
  line-height: 20px !important;
}
.DraftEditor-Container .DraftEditor-controls-row .DraftEditor-controls-row-inner .DraftEditor-styleButton:last-child {
  margin-right: 0;
}
.DraftEditor-Container .DraftEditor-controls-row .DraftEditor-controls-row-inner .DraftEditor-styleButton:hover,
.DraftEditor-Container .DraftEditor-controls-row .DraftEditor-controls-row-inner .DraftEditor-activeButton {
  color: #007cba;
  border-color: #007cba;
}
.DraftEditor-Container .toolbox {
  display: flex;
  align-items: center;
  height: 34px;
  border: 1px solid var(--border300, rgb(235, 235, 235));
  flex-grow: 1;
  padding: 0 5px;
}
.DraftEditor-Container .toolbox .components-button {
  height: auto;
  padding: 2px;
}
.DraftEditor-Container .border-style {
  border: 1px solid var(--border300, rgb(235, 235, 235));
  padding: 6px;
}

.DraftEditor-root {
  background: #fff;
  border: 1px solid var(--border300, rgb(235, 235, 235));
  padding: 15px;
}

.DraftEditor-editor {
  cursor: text;
  font-size: 16px;
  margin-top: 10px;
}

.DraftEditor-editor .public-DraftEditorPlaceholder-root,
.DraftEditor-editor .public-DraftEditor-content {
  margin: 0 -15px -15px;
  padding: 15px;
}

.DraftEditor-editor .public-DraftEditor-content {
  min-height: 100px;
}

.DraftEditor-hidePlaceholder .public-DraftEditorPlaceholder-root {
  display: none;
}

.DraftEditor-editor .DraftEditor-blockquote {
  border-left: 5px solid #eee;
  color: #666;
  font-family: "Hoefler Text", "Georgia", serif;
  font-style: italic;
  margin: 16px 0;
  padding: 10px 20px;
}

.DraftEditor-editor .public-DraftStyleDefault-pre {
  background-color: rgba(0, 0, 0, 0.05);
  font-family: "Inconsolata", "Menlo", "Consolas", monospace;
  font-size: 16px;
  padding: 20px;
}

.DraftEditor-controls {
  font-family: "Helvetica", sans-serif;
  font-size: 14px;
  margin-bottom: 5px;
  user-select: none;
}

.WrapperBlockRendering {
  position: relative;
  padding: 20px 10px 10px 10px;
  border: 1px dashed rgba(0, 124, 186, 0.5);
  margin: 10px 0;
}
.WrapperBlockRendering::before {
  content: attr(type);
  display: block;
  position: absolute;
  left: -1px;
  top: -1px;
  font-size: 0.6rem;
  background: #007cba;
  color: #fff;
  padding: 0px 10px;
}